$mainColor: blue;
// 애니매이션 시작 때 적용할 CSS
.tabAnimation-enter {
  opacity: 0;
}
// 애니메이션 동작 때 적용할 CSS
.tabAnimation-enter-active {
  opacity: 1;
  transition: all 1000ms;
}

@mixin my-alert-style() {
  background-color: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  height: 100%;
  margin: auto;
  // display: none;
  p {
    margin-bottom: 0;
  }
}
.hidden {
  animation: fade-out 2s;
}

.red {
  color: $mainColor;
}

.my-alert {
  @include my-alert-style();
}

.my-alert-white-yellow {
  @extend .my-alert;
  background-color: #ffe591;
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
